import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Input,
  Tooltip,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col,
  Divider,
  // message
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/salesOrderReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirectSO = "/dashboard/sales/sales-orders";

class SalesOrderReport extends Component {
  state = {
    filter: {
      id_customer: null,
      id_location: null,
      transaction_status: null,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      sort_by: "transaction_date",
      sort_type: "asc",
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getSalesOrderReports } = this.props;
    getSalesOrderReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.salesOrderReport.error) {
      notification("error", this.props.salesOrderReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.salesOrderReport.success) {
      notification("success", this.props.salesOrderReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getSalesOrderReports } = this.props;
    let filter = this.filterValue();

    getSalesOrderReports(1, value, filter, this.state.filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getSalesOrderReports } = this.props;
    this.setState({
      filter: {
        id_customer: null,
        id_location: null,
        transaction_status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc",
        pagination: null
      }
    });
    this.setState({ search: null });
    getSalesOrderReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.transaction_status) {
      string
        ? (string += "&transaction_status=" + filter.transaction_status)
        : (string += "transaction_status=" + filter.transaction_status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_customer) {
      string
        ? (string += "&id_customer=" + filter.id_customer)
        : (string += "id_customer=" + filter.id_customer);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getSalesOrderReports } = this.props;
    //const { current_page } = this.props.salesOrderReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getSalesOrderReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getSalesOrderReports } = this.props;
    this.setState({
      filter: {
        id_customer: null,
        id_location: null,
        transaction_status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc",
        pagination: null
      }
    });
    this.setState({ search: null });
    getSalesOrderReports();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateSalesOrderReport(id);
    } else {
      this.props.deactivateSalesOrderReport(id);
    }
  };

  // // Handle pagination
  // handlePageChange = page => {
  //   const { getSalesOrderReports } = this.props;
  //   const { search, filter } = this.props.salesOrderReport;
  //   getSalesOrderReports(page, search, filter);
  // };

  render() {
    let summariesColumns = [
      {
        title: getLanguage({ id: "totalTransaction" }),
        dataIndex: "total_data",
        rowKey: "total_data",
        //width: "10%",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "totalItem" }),
        dataIndex: "total_item",
        rowKey: "total_item",
        //width: "10%",
        align: "right",
        render: text => {
          const obj = {
            children: text,
            props: {}
          };
          if (!isNaN(text)) {
            obj.props.rowSpan = 2;
          }
          // These two are merged into above cell
          else {
            obj.props.rowSpan = 0;
          }
          return obj;
        }
        // parseFloat(text)
        //   .toFixed(0)
        //   .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
      },
      {
        title: getLanguage({ id: "totalQuantity" }),
        dataIndex: "total_qty",
        rowKey: "total_qty",
        //width: "10%",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: `${getLanguage({ id: "totalDiscount" })} (IDR)`,
        dataIndex: "total_discount",
        rowKey: "total_discount",
        //width: "20%",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: `${getLanguage({ id: "totalTax" })} (IDR)`,
        dataIndex: "total_tax",
        rowKey: "total_tax",
        //width: "20%",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: `${getLanguage({ id: "total" })} (IDR)`,
        dataIndex: "total_amount",
        rowKey: "total_amount",
        //width: "20%",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />
      }
    ];

    // Definisi kolom2 serta field2 API yang dipakai tabel
    let columns = [
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "sales_order_no",
        rowKey: "sales_order_no",
        width: "16%",
        //fixed: "left",
        render: (text, row) => {
          let className;
          switch (row.transaction_status) {
            case 4:
              className = "new";
              break;
            case 6:
              className = "new";
              break;
            case 1:
              className = "ordered";
              break;
            case 2:
              className = "received";
              break;
            case 3:
              className = "closed";
              break;
            case 5:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <TransNo>
                <Link to={`${redirectSO}/update/${row.id_sales_order}`}>
                  {text}
                </Link>
              </TransNo>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        date: true,
        rowKey: "transaction_date",
        width: "13%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "14%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.customer" }),
        dataIndex: "customer_name",
        rowKey: "customer_name",
        width: "11%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "totalQuantity" }),
        dataIndex: "total_qty",
        rowKey: "total_qty",
        width: "9.5%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: `${getLanguage({ id: "totalDiscount" })} (IDR)`,
        dataIndex: "total_discount",
        rowKey: "total_discount",
        width: "10%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: `${getLanguage({ id: "totalTax" })} (IDR)`,
        dataIndex: "total_tax",
        rowKey: "total_tax",
        width: "9.5%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: `${getLanguage({ id: "total" })} (IDR)`,
        dataIndex: "total_amount",
        rowKey: "total_amount",
        width: "10%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "orderedBy" }),
        dataIndex: "ordered_by",
        rowKey: "ordered_by",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status_string",
        rowKey: "status_string",
        width: "9%",
        align: "center",
        render: (text, row) => {
          let className;
          let display;
          switch (row.transaction_status) {
            case 4:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
            case 1:
              className = "ordered";
              display = getLanguage({ id: "ordered" });
              break;
            case 2:
              className = "received";
              display = getLanguage({ id: "delivered" });
              break;
            case 3:
              className = "closed";
              display = getLanguage({ id: "closed" });
              break;
            case 5:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    // Data setup
    const { salesOrderReport } = this.props;
    const {
      sales_order_reports,
      locations,
      customers,
      filter,
      filter_select
    } = salesOrderReport;
    //console.log(this.props.salesOrderReport.base_currency); //console.log(this.state);
    const dataSource = [];
    const summariesDataSource = [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    sales_order_reports.map(data => {
      let status_string;
      if (data.transaction_status === 1) {
        status_string = getLanguage({ id: "ordered" });
      } else if (data.transaction_status === 2) {
        status_string = getLanguage({ id: "delivered" });
      } else if (data.transaction_status === 3) {
        status_string = getLanguage({ id: "closed" });
      } else if (data.transaction_status === 5) {
        status_string = getLanguage({ id: "cancelled" });
      } else {
        status_string = getLanguage({ id: "new" });
      }
      dataSource.push({
        ...data,
        status_string
      });
    });

    const { loading, loadingForeign } = this.props.salesOrderReport;

    summariesDataSource.push({
      // name: messages.all_page ? messages.all_page : "All Page",
      total_amount: salesOrderReport.total_amount
        ? salesOrderReport.total_amount
        : 0,
      total_discount: salesOrderReport.total_discount
        ? salesOrderReport.total_discount
        : 0,
      total_qty: salesOrderReport.total_qty ? salesOrderReport.total_qty : 0,
      total_tax: salesOrderReport.total_tax ? salesOrderReport.total_tax : 0,
      total_item: salesOrderReport.total_item ? salesOrderReport.total_item : 0,
      total_data: salesOrderReport.total ? salesOrderReport.total : 0
    });

    const fileName = getLanguage({ id: "salesOrderReports.pageHeader" })

    // const columns = columns.map(col => {
    //   return col;
    // });

    // const summariesColumns = summariesColumns.map(col => {
    //   return col;
    // });

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="salesOrderReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="salesOrderReports.pageHeader" />}
              summariesColumns={summariesColumns}
              summariesData={summariesDataSource}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => document.title = siteConfig.documentTitle}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                  "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              margin=".7cm"
              scale={0.51}
              summariesColumns={summariesColumns}
              summariesData={summariesDataSource}
              title="salesOrderReports.pageHeader"
            />

            <ExcelWrapper
              title="salesOrderReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "transactionNo" }) +
                ", " +
                getLanguage({ id: "createdBy" }) +
                ", " +
                getLanguage({ id: "orderedBy" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="customers.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_customer"
                      )}
                      value={
                        this.state.filter.id_customer
                          ? this.state.filter.id_customer
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={<IntlMessages id="select.customer" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {customers.map((data, key) => (
                        <Option key={key} value={data.id_customer}>
                          {`${data.customer_code} (${data.customer_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>

                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="stockValuationReports.sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "transaction_date"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="transaction_date">
                            <IntlMessages id="salesOrderReports.filter.date" />
                          </Option>
                          <Option key={2} value="sales_order_no">
                            <IntlMessages id="salesOrders.salesOrderNo" />
                          </Option>
                          <Option key={3} value="location_name">
                            <IntlMessages id="sidebar.location" />
                          </Option>
                          <Option key={4} value="customer_name">
                            <IntlMessages id="sidebar.customer" />
                          </Option>
                          <Option key={5} value="total_amount">
                            <IntlMessages id="totalAmount" />
                          </Option>
                          <Option key={6} value="total_qty">
                            <IntlMessages id="totalQuantity" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Row gutter={1} type="flex">
                      <Col span={8}>
                        <label>
                          <IntlMessages id="status" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "transaction_status"
                          )}
                          value={
                            this.state.filter.transaction_status
                              ? this.state.filter.transaction_status
                              : []
                          }
                          style={{ width: "95%" }}
                          placeholder="Status"
                          optionFilterProp="children"
                          filterOption={true}
                          // loading={this.props.location.loadingFilterSites}
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option value={4}>
                            {getLanguage({ id: "new" })}
                          </Option>
                          <Option value={1}>
                            {getLanguage({ id: "ordered" })}
                          </Option>
                          <Option value={2}>
                            {getLanguage({ id: "delivered" })}
                          </Option>
                          <Option value={3}>
                            {getLanguage({ id: "closed" })}
                          </Option>
                          <Option value={5}>
                            {getLanguage({ id: "cancelled" })}
                          </Option>
                        </Select>
                      </Col>
                      {/* <Col span={8}>
                        <label>
                          <IntlMessages id="dataPerPage" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "pagination"
                          )}
                          value={
                            this.state.filter.pagination
                              ? this.state.filter.pagination
                              : page_size
                          }
                          style={{ width: "80%" }}
                          // placeholder={
                          //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value={10}>
                            10
                          </Option>
                          <Option key={2} value={25}>
                            25
                          </Option>
                          <Option key={3} value={50}>
                            50
                          </Option>
                          <Option key={4} value={100}>
                            100
                          </Option>
                        </Select>
                      </Col> */}
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clicksearchFilter" />} />
            ) : sales_order_reports.length === 0 &&
              !this.props.salesOrderReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="salesOrderReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="summaries" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={summariesDataSource}
                    columns={summariesColumns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    salesOrderReport: state.salesOrderReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(SalesOrderReport);
